













































import ut from '../../utils/index.js';
export default {
	data() {
		return {
			reformtype:0,
			check1:-1,
			check:-1,
			classList:[],
			server: [],
			homeType:[],
			bigType:[],
			serverIndex:'-1',
			static:''
		}
	},
	onLoad() {
		this.static=ut.static;
		this.req_class();
		this.req_bigType();
		this.req_homeType();
	},
	methods: {
		cg_serverIndex(index){
			this.serverIndex=index
		},
		go_mine_infchange() {
			wx.navigateTo({
				url: '../mine/infchange'
			})
		},
		go_mine_apply() {
			wx.navigateTo({
				url: '../mine/apply'
			})
		},
		cg_check(index){
			if(this.check==index){
				this.check=-1;
				return;
			}
			this.check=index
		},
		cg_check1(index){
			if(this.check1==index){
				this.check1=-1;
				return;
			}
			this.check1=index;
		},
		cg_reformtype(index,id){
			this.reformtype=index;
			this.req_server(id)
		},
		req_class(){
			ut.request({
				url: "renovation/wholeHomeClassList",
				method:'get'
			}).then(data=>{
				this.classList=data;
				return data
			}).then(data=>{
				if(data[0]){
					this.req_server(data[0].id)
				}
			})
		},
		req_bigType(){
			ut.request({
				url: "renovation/houseAreaList",
				method:'get'
			}).then(data=>{
				this.bigType=data;
			})
		},
		req_homeType(){
			ut.request({
				url: "renovation/houseTypeList",
				method:'get'
			}).then(data=>{
				this.homeType=data;
			})
		},
		req_server(id){
			ut.request({
				data:{
					classId:id
				},
				url: "renovation/wholeHomeTypeList",
				method:'get'
			}).then(data=>{
				this.server=data;
			})
		},
		req_make(){
			if(this.serverIndex=='-1'){
				ut.totast('请选择改造类型');
				return;
			}
			if(this.check=='-1'){
				ut.totast('请选择户型');
				return;
			}
			if(this.check1=='-1'){
				ut.totast('请选择面积');
				return;
			}
			wx.setStorageSync('remakeinf',{
				name:this.server[this.serverIndex].name,
				wholeHomeTypeId:this.server[this.serverIndex].id,
				houseTypeId:this.homeType[this.check].id,
				picture:ut.static+this.server[this.serverIndex].picture,
				areaTypeId:this.bigType[this.check1].id
			})
			if(wx.getStorageSync('token')){
				wx.navigateTo({
					url: '../home/remake?type=1'
				})
			}else{
				wx.navigateTo({
					url: '../mine/login'
				})
			}
		}
	}
}
